import React, { Component } from "react";
import HeaderCard from "../HeaderCard";
import { getListBarang } from "../actions/kodeBarangAction";
import { connect } from "react-redux";
import FormCetakBarcodeModel from "./FormCetakBarcodeModel";
import { AxiosMasterPost } from "../Axios";
import { NotifError, NotifSucces } from "../GlobalComponent/notification";

class CetakBarcodeModel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }

  paddingNumberGW(str) {
    var pad = "0000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  paddingNumberNW(str) {
    var pad = "000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  handleSubmit(data) {
    var simpan = {
      qty: data.qty || 1,
      k: parseFloat(data.k || 0) || 0,
      ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
      bruto: parseFloat(data.bruto) || 0,
      disc_persen: data.discGlobalPr || 0,
      kode_jenis: data.kode_barang, //kodebarang
      p: parseFloat(data.p || 0),
      pkg: parseFloat(data.pkg || 0),
      harga_jual : data.harga_jual === "undefined" ? 0 : data.harga_jual || 0
    };
    AxiosMasterPost("barcode-model/add", simpan).then((res)=> {
      NotifSucces("Barcode Berhasil Disimpan")
      let feedback = `AEX0ARA3H001V001CS3#E5APM0PO3+00YE0IG1PH0A102000671Z##                            APSWKesther grosir LB`;
      feedback += `%2H0637V0187L0102P02S${res.data.nama_jenis}`;
      // BARCODE
      feedback += `%2H0622V0158BG01067>H${res.data.kode_barcode}`;
      feedback += `%2H0622V0087L0101P02S${res.data.kode_barcode}`;
      // BARCODE
      // feedback += `%2H0650V0058L0101P02S${"KC"}%2H0622V0065L0102P02S${parseFloat(
      //   data.kadar_cetak || 0
      // )}`;
      feedback += `%2H0650V0028L0101P02S${"Bruto"}%2H0622V0038L0102P02S${parseFloat(
        data.bruto || 0
      )}`;
      feedback += `&01NiceOvlZA/01~A0Q1Z##  ##`;
      document.getElementById("myInput").value = feedback;
      const element = document.createElement("a");
      const file = new Blob([document.getElementById("myInput").value], {
        type: "text/plain;charset=utf-8",
      });
      element.href = URL.createObjectURL(file);
      element.download = "autoprint_barcode.txt";
      document.body.appendChild(element);
      element.click();
    }).catch((err)=> NotifError("Terjadi Kesalahan saat menyimpan data"))
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Cetak Barcode Model" />
          <div className="card-body">
            <div className="row">
              <div className="col-lg-12">
                {/* BARCODE 1 */}
                {/* <CetakBarcodeModelHead
                  onSubmit={(data) => this.handleSubmit(data)}
                /> */}
                <FormCetakBarcodeModel onSubmit={(data) => this.handleSubmit(data)} />
              </div>
              <div className="d-none">
                <textarea id="myInput"></textarea>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect((state) => {
  return {
    kodeBarang: state.provinsi.kodeBarang,
  }
})(CetakBarcodeModel);
