import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { renderField, renderFieldSelect } from "../renderField";
import Skeleton from "react-loading-skeleton";
import { getListBarang } from "../actions/kodeBarangAction";
import {
  setFocus,
} from "../helper";
import { AxiosMasterGet } from "../Axios";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormCetakBarcodeModel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kodeBarang: [],
      non_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
      image: ""
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    // getTimbangan(this.props.change);
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
    this.props.change("discGlobalGr", this.props.discGlobalGr);
    this.props.change(
      "nettoDiscGlobalExt",
      (this.props.netto - this.props.discGlobalGr).toFixed(3)
    );
  }

  pilihBarang(data) {
    let param = data;
    let hasil = this.props.KodeBarangList.filter(
      (data) => data.kode_jenis === param
    );
    if(hasil[0].kode_kelompok === "AKSESORIS"){
      this.setState({
        non_aksesoris: "col-lg-12 row d-none",
        aksesoris: "col-lg-12 row ",
      })
    } else {
      this.setState({
        non_aksesoris: "col-lg-12 row ",
        aksesoris: "col-lg-12 row d-none",
      })
    }
    this.setState({
      kodeJenis: hasil[0].kode_jenis,
      namaJenis: hasil[0].nama_jenis,
      kadarCetak: hasil[0].kadar_cetak,
      kadarJual: hasil[0].kadar_jual,
      harga_jual : hasil[0].harga_jual
    });
    this.props.change("nama_model", hasil[0].nama_jenis);
    this.props.change("jenis_model", hasil[0].kode_kategori);
    this.props.change("kode_intern", hasil[0].kode_intern);
    this.props.change("kode_kelompok", hasil[0].kode_kelompok);
    this.setKadar();
    AxiosMasterGet("jenis/get/by-kode-jenis/" + param).then((res) => {
      this.setState(
        {
          image : res.data.image
        }
      )
    } )
    setFocus("P");
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
    this.props.change("harga_jual", this.state.harga_jual);
  }

  setBruto() {
    this.props.change("bruto", localStorage.getItem("Timbangan"));
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            {String(this.props.KodeBarangList) !== "" ? (
              <Field
                name="kode_barang"
                label="Kode Model"
                component={renderFieldSelect}
                className="form-control"
                onChange={(data) => this.pilihBarang(data)}
                onBlur={this.setKadar()}
                options={this.props.KodeBarangList.filter(
                  (data) => data.kode_kelompok === "PERHIASAN" || data.kode_kelompok === "AKSESORIS"
                ).map((kode) => {
                  let data = {
                    value: kode.kode_jenis,
                    name: kode.nama_jenis,
                  };
                  return data;
                })}
              />
            ) : (
              <Skeleton width={"100%"} height={50} />
            )}
          </div>
          <div className="col-lg-6"></div>
          <div className="col-lg-12 row">
            <div className="col-lg-4"></div>
            <div className="col-lg-4 text-center">
            <img src={this.state.image} width="50%" alt="Foto Barang" />
            </div>
          </div>
          <div className="col-lg-12 row">
          <div className="col-md-3 col-lg-3">
            <Field
              name="nama_model"
              label="Nama Model"
              type="text"
              readOnly
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="jenis_model"
              label="Jenis"
              type="text"
              readOnly
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="kode_intern"
              label="Kode Intern"
              type="text"
              readOnly
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="kode_kelompok"
              label="Kode Kelompok"
              type="text"
              readOnly
              component={renderField}
            />
          </div>
          </div>
          <div className={this.state.non_aksesoris}>
          <div className="col-md-3 col-lg-3">
            <Field
              name="kadar"
              label="Cap Kode"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field name="p" label="P" type="number" component={renderField} />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field name="k" label="K" type="number" component={renderField} />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="pkg"
              label="Pkg (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="bruto"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onChange={this.onclick1()}
              // onChange={this.setBruto()}
              // onBlur={this.setBruto()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="kadarTransaksi"
              label="Kode Tukar"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="netto"
              label="Netto (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="gross"
              label="Gross (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="discGlobalPr"
              label="Diskon (%)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="discGlobalGr"
              label="Diskon (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="nettoDiscGlobalExt"
              label="Netto - Diskon (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="rp"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          </div>
          <div className={this.state.aksesoris}>
          <div className="col-md-3 col-lg-3">
            <Field
              name="qty"
              label="Qty"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <label htmlFor="">Harga Jual</label>
            <Field
              name="harga_jual"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormCetakBarcodeModel = reduxForm({
  form: "FormCetakBarcodeModel",
  enableReinitialize: true,
})(FormCetakBarcodeModel);
const selector = formValueSelector("FormCetakBarcodeModel"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, pkg, discGlobalPr } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg",
    "discGlobalPr"
  );
  return {
    netto: (
      parseFloat(bruto || 0) *
      (parseFloat(kadarTransaksi || 0) / 100)
    ).toFixed(3),
    gross: parseFloat(bruto || 0) + parseFloat(pkg || 0),
    discGlobalGr: (
      parseFloat(bruto || 0) *
      (parseFloat(discGlobalPr || 0) / 100)
    ).toFixed(3),
    KodeBarangList: state.provinsi.kodeBarang,
    initialValues: {
      discGlobalPr: 0,
      p: 0,
      k: 0,
      pkg: 0,
      bruto: 0,
    },
  };
})(FormCetakBarcodeModel);
